import React, { useState, lazy } from "react";
import s from "./Registration.module.scss";
import { useNavigate } from "react-router-dom";
import { getAuth, createUserWithEmailAndPassword, updateProfile } from "firebase/auth";
const FormForRegistr = lazy(() => import("../../components/FormForRegistr/FormForRegistr"));

function Registration() {
  const [userData, setUserData] = useState({ email: "", password: "", name: "" });
  const [error, setError] = useState("");
  const navigate = useNavigate();
  async function handleLogin(e) {
    e.preventDefault();
    const auth = getAuth();

    try {
      const res = await createUserWithEmailAndPassword(auth, userData.email, userData.password);
      await updateProfile(res.user, {
        displayName: userData.name,
      });
      console.log(res);
      setUserData({ email: "", password: "", name: "" });
      navigate("/");
    } catch (error) {
      error.message && setError(error.message);
    }
  }

  return (
    <div className={s.registr}>
      {error && <p className="error">{error}</p>}

      <FormForRegistr handleLogin={handleLogin} userData={userData} setUserData={setUserData} />
    </div>
  );
}
export default Registration;
